.SyncBar {
    position: fixed;
    bottom: 5px;
    width: 100%;
    border: 1px solid #eee;
    box-shadow: 0 2px 3px #ccc;
    padding: 10px;
    box-sizing: border-box;
    display: flex;
    flex-direction: row;
    align-items: baseline;
}

.SyncBar > div {
  margin-left: auto;
}


